.contentList {
	margin-top:20px;
	width:100%;
	margin-right:0px;
	margin-left:0px;
}

.row {
	margin-right:0px;
	margin-left:0px;
}
.content { 
	position: relative;
	z-index:111;
	width:100%;
}

.content:hover .thumbnail {
	opacity: 0.3;
}

.thumbnail {
	width:100%;
}

.middle { 
	position: absolute; 
	top: 35%;
	left: 0;
	z-index:10;
}

.text {
	color:white;
	font-size:16px;
}
.my-iframe {display:block;border:none;height:40vw;width:100%;}
.content-view-iframe { height:55vw; }
